"use client";
import { useEffect, useState } from "react";
import axios from "axios";
import { Button } from "./ui/button";

const API_BASE_URL = "http://127.0.0.1:8000/api";

interface Document {
  document_id: string;
  filename: string;
  status: string;
  uploaded_at: string;
}

export default function DocumentList({ onSelect }: { onSelect: (id: string) => void }) {
  const [documents, setDocuments] = useState<Document[]>([]);
  const [loading, setLoading] = useState(false);

  const fetchDocuments = async () => {
    try {
      setLoading(true);
      const res = await axios.get(`${API_BASE_URL}/documents/`);
      setDocuments(res.data.documents || []);
    } catch (error) {
      console.error("Error fetching documents:", error);
    } finally {
      setLoading(false);
    }
  };

  const deleteDocument = async (id: string) => {
    if (!confirm("Are you sure you want to delete this document?")) return;
    try {
      await axios.delete(`${API_BASE_URL}/documents/${id}`);
      setDocuments(docs => docs.filter(d => d.document_id !== id));
    } catch (error) {
      console.error("Delete Error:", error);
      alert("Failed to delete document");
    }
  };

  useEffect(() => {
    fetchDocuments();
  }, []);

  return (
    <div className="p-6 bg-white rounded-xl shadow-md border">
      <h2 className="text-xl font-semibold mb-3 text-gray-800">Uploaded Documents</h2>
      {loading && <p>Loading...</p>}

      {documents.length === 0 && !loading && <p>No documents uploaded yet.</p>}

      <ul className="space-y-3">
        {documents.map((doc) => (
          <li
            key={doc.document_id}
            className="flex justify-between items-center border-b pb-2"
          >
            <div>
              <p className="font-medium">{doc.filename}</p>
              <p className="text-xs text-gray-500">Status: {doc.status}</p>
            </div>
            <div className="flex gap-2">
              <Button
                onClick={() => onSelect(doc.document_id)}
                className="bg-green-600 hover:bg-green-700"
              >
                Ask
              </Button>
              <Button
                onClick={() => deleteDocument(doc.document_id)}
                className="bg-red-600 hover:bg-red-700"
              >
                Delete
              </Button>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}
