"use client";
import { useState } from "react";
import axios from "axios";
import { Button } from "./ui/button";
import { Input } from "./ui/input";

const API_BASE_URL = "http://127.0.0.1:8000/api";

export default function AskQuestionBox({ selectedDocId }: { selectedDocId: string }) {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [loading, setLoading] = useState(false);

  const handleAsk = async () => {
    if (!question) return alert("Enter a question");

    try {
      setLoading(true);
      const res = await axios.post(`${API_BASE_URL}/qa/query`, {
        document_id: selectedDocId,
        question: question,
      });
      setAnswer(res.data.answer || JSON.stringify(res.data, null, 2));
    } catch (error: any) {
      console.error("Ask Error:", error);
      alert("Error fetching answer");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-6 bg-white rounded-xl shadow-md border">
      <h2 className="text-xl font-semibold mb-3 text-gray-800">Ask Question</h2>
      <Input
        placeholder="Type your question here..."
        value={question}
        onChange={(e) => setQuestion(e.target.value)}
        className="mb-3"
      />
      <Button onClick={handleAsk} disabled={loading}>
        {loading ? "Thinking..." : "Ask Question"}
      </Button>

      {answer && (
        <pre className="bg-gray-100 mt-4 p-3 rounded-md text-sm overflow-x-auto">
          {answer}
        </pre>
      )}
    </div>
  );
}
